<template>
  <div id="myPA" @click="clickP">
    <div class="P">
      <div class="PP">问：</div>
      <div class="Pp">
        <p>{{data.p}}</p>
      </div>
    </div>
    <div class="A">
      <div class="AA">答：</div>
      <div class="Ap">
        <p>{{data.a}}</p>
      </div>
    </div>
    <div class="myPA_foot">
      <p class="foot_time">{{data.time}}</p>
      <p class="foot_service">{{data.service}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'myPA',
  props: {
    data: Object
  },
  methods: {
    clickP () {
      this.$router.push({
        name: 'consult',
        params: {
          PA: this.data
        }
      })
    }
  }
}
</script>

<style scoped>
  #myPA {
    height: 10rem;
    background-color: #FFFFFF;
    width: 23rem;
    position: relative;
    margin: 1rem;
    border-radius: 4px;
    font-size: 0.97rem;
  }

  #myPA .P {
    height: 3.2rem;
    position: relative;
  }

  #myPA .P .PP {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    color: #FF332E;
    text-align: center;
    margin: 1rem;
    padding: 0 0 0 0.2rem;
    position: absolute;
  }

  #myPA .P .Pp {
    display: inline-block;
    width: 18rem;
    position: absolute;
    left: 4rem;
  }

  #myPA .P .Pp p {
    width: 18rem;
    height: 3rem;
    line-height: 1.5rem;
    display: -webkit-box;
    text-overflow: ellipsis;
    overflow: hidden;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  #myPA .A {
    position: absolute;
    top: 3rem;
  }

  #myPA .A .AA {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    color: #0078FF;
    text-align: center;
    margin: 1rem;
    padding: 0 0 0 0.2rem;
    position: absolute;
  }

  #myPA .A .Ap {
    display: inline-block;
    width: 18rem;
    position: absolute;
    left: 4rem;
  }

  #myPA .A .Ap p {
    width: 18rem;
    height: 3rem;
    line-height: 1.5rem;
    display: -webkit-box;
    text-overflow: ellipsis;
    overflow: hidden;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    color: #333333;
  }

  #myPA .myPA_foot {
    position: absolute;
    bottom: 0;
    color: #999999;
  }

  #myPA .myPA_foot p {
    display: inline-block;
    margin: 0 1rem 1rem;
    font-size: 0.8rem;
    width: 9rem;
  }

  #myPA .myPA_foot .foot_time {

  }

  #myPA .myPA_foot .foot_service {
    text-align: right;
  }
</style>



